import React from 'react'

import styles from './About.css'

const About = () => (
  <div>
    <h1 className={styles.heading}>I am Phyu Phyu Aung!</h1>
    <p className={styles.bio}>This section is all about me,yay!</p>
  </div>
)

export default About



